<template>
    <div class="SettingContent">
      <HomePageTop  :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div class="textareaHome">
        <textarea name="" id="" cols="30" rows="10" placeholder="请输入你的问题" @input="textareaText" v-model="textareaHome"></textarea>
      </div>
      <div class="public-btn mt60 mr30 ml30" :class="SubmitClassbox==true?'public-btn':'public-btnNo'" @click="submit">提交</div>

    </div>

</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "SettingContent",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: {name: "我要咨询",left: true},
              textareaHome:"",
              SubmitClassbox:false,
            }
        },
        methods: {
          //@input
          textareaText(){
            if(this.textareaHome!=""){
              this.SubmitClassbox=true;
            }
          },
          //提交
          submit(){
            if( this.SubmitClassbox=true){
              let date = new Date();
              let year = date.getFullYear(); // 年
              let month = date.getMonth() + 1; // 月
              let day = date.getDate(); // 日
              let week = date.getDay(); // 星期

              let hour = date.getHours(); // 时
              hour = hour < 10 ? "0" + hour : hour; // 如果只有一位，则前面补零
              let minute = date.getMinutes(); // 分
              minute = minute < 10 ? "0" + minute : minute; // 如果只有一位，则前面补零
              let second = date.getSeconds(); // 秒
              second = second < 10 ? "0" + second : second; // 如果只有一位，则前面补零;
              let currentdate;
              currentdate = `${year}-${month}-${day} ${hour}:${minute}:${second}`;
              console.log(currentdate);
              let ojbs={time:currentdate,text:this.textareaHome};
              console.log(ojbs);
              this.$store.commit("message",ojbs);
            }
          },
        },
        created() {

        }
    }

</script>

<style>
  .textareaHome{
    width: 100%;
    height: 3.6rem;
    background: #fff;
    margin-top: .3rem;
  }
  .textareaHome textarea{
    padding: .3rem;
    width: 100%;
    border: none;
    resize:none;
    height: 100%;

    font-size: .28rem;

  }

</style>



